@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400&display=swap");

body {
  background-color: beige;
}

div.app {
  width: 100%;
}

div.header {
  color: blue;
  text-align: center;
  margin: 5px;
  font-family: "Montserrat", sans-serif;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 10%;
}

.navcontact {
  list-style: none;
  padding: 10px;
  margin: 5px;
}

.navcontact li {
  display: inline-block;
  list-style: none;
  padding: 0px 10px;
}

.navcontact li a {
  transition: all 0.3s ease 0s;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-size: 16px;
  text-decoration: none;
}

.navcontact li a:hover {
  color: red;
}

.rescontainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  max-width: 800px;
  margin: 0 50px 0px 50px;
}


.edwork li {
  list-style: none;
  padding: 0px 10px;
  text-decoration: none;
  line-height: 1.5;
}




.rescontainer-2 {
  display: flex;
  align-items: center;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  max-width: 800px;
  margin: 0 70px 0px 70px;
}
.aboutme {
  width: 300px;
}
.careergoal {
  width: 300px;
}

p {
  text-indent: 50px;
  line-height: 1.5;
}



